.form input.focus-visible:focus.focus-visible:focus,
.form textarea.focus-visible:focus.focus-visible:focus {
  outline: 1px solid #ffffff;
  outline-offset: 2px;
}
.form input::placeholder,
.form textarea::placeholder {
  color: #ffffff;
}
@media (max-width: 1023px) {
  .form {
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .form {
    grid-template-columns: 1fr;
  }
}
.form label.is-invalid input,
.form label.is-invalid textarea {
  outline: 2px solid #ff1553;
}
.form label.is-invalid input:focus, .form label.is-invalid input:hover,
.form label.is-invalid textarea:focus,
.form label.is-invalid textarea:hover {
  outline: 2px solid rgba(255, 21, 83, 0.5);
}
.form--modal {
  grid-template-columns: 1fr;
  padding-left: 0;
}

label {
  position: relative;
}
label .input-message {
  position: absolute;
  top: -50px;
  left: 0;
  font-size: 15px;
  line-height: 15px;
}
label .input-message.is-invalid {
  color: #ff1553;
}
label .input-message.is-valid {
  color: #2c39f2;
}
@media (max-width: 1023px) {
  label .input-message {
    top: -35px;
  }
}

.form__textarea {
  grid-column: 1/-1;
}
.form__textarea textarea {
  min-height: 162px;
  padding-top: 26px;
}
@media (max-width: 1023px) {
  .form__textarea textarea {
    min-height: 155px;
    padding-top: 31px;
  }
}
@media (max-width: 767px) {
  .form__textarea textarea {
    margin-bottom: 2px;
  }
}

.form__button {
  display: block;
  margin-right: 10px;
  padding: 16px 25px 11px;
  text-align: center;
  background-color: #041427;
}
@media (max-width: 1023px) {
  .form__button {
    margin-right: 55px;
    margin-top: 10px;
    padding: 17px 25px 10px;
  }
}
@media (max-width: 767px) {
  .form__button {
    margin-right: 0;
    padding: 15px 25px 11px;
  }
}
.form__button--modal {
  margin-right: 0;
  padding: 14px 25px 11px;
}
@media (max-width: 1023px) {
  .form__button--modal {
    margin-right: 0;
    padding: 14px 25px 11px;
  }
}

.form__checkbox {
  position: relative;
  display: flex;
  font-family: "PT Mono", "Arial", sans-serif; /*для подписи к чекбоксу*/
  font-size: 14px;
  line-height: 15px;
  font-weight: 400;
  color: #ffffff;
  opacity: 0.8;
  align-self: flex-end;
}
@media (max-width: 1023px) {
  .form__checkbox {
    align-self: center;
  }
}
.form__checkbox label {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 26px;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .form__checkbox label {
    padding-top: 7px;
  }
}
@media (max-width: 767px) {
  .form__checkbox label {
    padding-top: 5px;
    padding-left: 30px;
  }
}

.form__mark {
  position: absolute;
  left: 0;
  width: 15px;
  height: 15px;
  margin-left: 10px;
  border: 1px solid #ffffff;
  background-color: transparent;
}
@media (max-width: 1023px) {
  .form__mark {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .form__mark {
    margin-left: 5px;
  }
}

[type=checkbox]:checked + .form__mark {
  background-image: url("../../img/svg/check.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.form__label {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15px;
  font-weight: 400;
  color: #ffffff;
  opacity: 0.8;
  margin-left: 10px;
}
@media (max-width: 1023px) {
  .form__label {
    margin-left: 0;
  }
}

.footer {
  background-color: #041827;
  padding-top: 40px;
  padding-bottom: 42px;
}
@media (max-width: 1023px) {
  .footer {
    padding-bottom: 59px;
  }
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 2.32fr;
  column-gap: 10px;
}
@media (max-width: 1023px) {
  .footer__container {
    grid-template-columns: 1fr;
    padding-right: 38px;
  }
}

@media (max-width: 1023px) {
  .footer__social-wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 31px;
  }
}
@media (max-width: 767px) {
  .footer__social-wrapper {
    flex-direction: column;
    margin: auto;
    align-items: center;
  }
}

.footer__logo {
  display: flex;
  margin: 0 0 30px;
  align-items: center;
}
@media (max-width: 1023px) {
  .footer__logo {
    flex-direction: row;
    margin: 0 23px 0 0;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin: 0;
  }
}

.footer__nav-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 48px;
  padding-top: 7px;
}
@media (max-width: 1279px) {
  .footer__nav-wrapper {
    gap: 70px;
  }
}
@media (max-width: 1023px) {
  .footer__nav-wrapper {
    gap: 0;
    justify-content: space-between;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .footer__nav-wrapper {
    flex-direction: column;
    justify-content: left;
    padding-top: 0;
  }
}

.footer__nav h2,
.footer__address h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 15px;
  font-weight: 400;
  letter-spacing: 0.05em;
  color: #ffffff;
  margin-bottom: 21px;
  padding: 0;
}
@media (max-width: 767px) {
  .footer__nav h2,
.footer__address h2 {
    text-align: center;
    line-height: 16px;
  }
}

@media (max-width: 1023px) {
  .footer__nav {
    width: 230px;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    flex-direction: column;
    width: 100%;
  }
}

.footer__nav-content {
  display: flex;
  gap: 47px;
}
@media (max-width: 1279px) {
  .footer__nav-content {
    flex-direction: column;
    gap: 0;
  }
}

.footer__list {
  width: 203px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .footer__list {
    flex-direction: row;
    margin: 0 23px 0 0;
  }
}
@media (max-width: 767px) {
  .footer__list {
    margin: 0;
  }
}
.footer__list--address {
  width: 310px;
}
@media (max-width: 767px) {
  .footer__list--address {
    width: 100%;
  }
}

.footer__item {
  margin-bottom: 6px;
}

.footer__link {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 14px;
    line-height: 19px;
  }
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    color: #00b2ff;
  }
}
.footer__link.focus-visible:focus {
  color: #00b2ff;
}

.footer__address address {
  font-style: normal;
}

@media (max-width: 767px) {
  .logo img {
    padding-top: 9px;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 150px;
  padding: 0;
  list-style-type: none;
  margin-left: 10px;
}
@media (max-width: 1023px) {
  .social__list {
    width: 165px;
    gap: 16px;
    justify-content: center;
  }
}

.social__item {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
}

.social__link {
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__link:hover, .social__link:focus {
  background-color: #041827;
  border-radius: 50%;
  opacity: 0.8;
}

